import modal from './components/Modal';
import { EventEmitter } from 'fbemitter';
import {
    ModalTypes,
    ThemeTypes,
} from './Constants';

export default class {
    alert_title = '';
    confirm_title = '';
    constructor({ Lang } = {}) {
        this.Lang = Lang || window.Lang || {};
        this.setAlert();
        this.setConfirm();
    }

    setAlert = () => {
        const { General = {} } = this.Lang;
        const { alert_title = '', confirm_title = '' } = General;
        this.alert_title = alert_title;
        this.confirm_title = confirm_title;
        this.alertModal = new modal(
            { title: alert_title, type: ModalTypes.ALERT, theme: ThemeTypes.BLUE },
            { isCommon: true }
        );
    }

    getAlert = () => {
        return this.alertModal;
    }

    getConfirm = () => {
        return this.alertConfirm;
    }

    setConfirm = () => {
        const { General = {} } = this.Lang;
        const { alert_title = '', confirm_title = '' } = General;
        this.alert_title = alert_title;
        this.confirm_title = confirm_title
        this.confirmModal = new modal(
            { title: confirm_title, type: ModalTypes.CONFIRM, theme: ThemeTypes.BLUE },
            { isCommon: true }
        );
    }

    createModal(props) {
        return new modal(props);
    }

    alert = (content, title, options, extend) => {
        this.alertModal.modalData = Object.assign({}, this.alertModal.modalData[0], {
            content,
            options,
            title: title || this.alert_title,
        }, extend);
        this.alertModal.show();
        this.alertModal.on('hide', () => {
            this.alertModal.off('hide');
        })
        return this.alertModal;
    }

    confirm = (content, title, options, extend) => {
        const { Menus = {} } = Lang;
        const { popup_notice = "" } = Menus;
        this.confirmModal.modalData = Object.assign({}, this.confirmModal.modalData[0], {
            content,
            options,
            title: title || this.confirm_title,
        }, extend);
        this.confirmModal.show();
        return new Promise((resolve, reject) => {
            this.confirmModal.on('click', function (event) {
                let isConfirm = event === 'ok';
                resolve(isConfirm);
            });
            this.confirmModal.on('hide', function () {
                resolve(false);
            });
        });
    }
}

export var __useDefault = true;



